<template>
  <div class="orderdetail">
      <div class="flow">
        <flow>
            <flow-state state="1" title="已付款" is-done></flow-state>
            <flow-line is-done></flow-line>
            <flow-state state="2" is-done title="备货中"></flow-state>
            <flow-line tip="进行中"></flow-line>
            <flow-state state="3" title="配送中"></flow-state>
            <flow-line></flow-line>
            <flow-state state="4" title="已送达"></flow-state>
            <flow-line></flow-line>      
            <flow-state state="5" title="已评价"></flow-state>
        </flow>
      </div>
      <div class="addressee paddi">
          <p class="top">
              <span class="two_t_h name">王尼玛</span>
              <span class="two_t_h">186****6666</span>
          </p>
          <p class="three_t_m">
              广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层
          </p>
      </div>
      <div class="goods paddi">
          <p class="clearfix state">
              <span class="fl three_t_m">商品数量：</span>
              <span class="fl three_t_h">1</span>
              <span class="fr three_t_h">已完成</span>
          </p>
          <div class="bottom clearfix">
              <img src="../../assets/test/goods.png" class="fl" alt="">
              <div class="right fl">
                  <p class="two_t_h">得乐蛋白五只装礼盒</p>
                  <p class="three_t_m guige">180g/盒；五盒/套</p>
                  <p class="three_t_m">¥57.00</p>
              </div>
          </div>
      </div>
      <div class="fapiao paddi clearfix">
          <span class="fl two_t_h">发票</span>
          <span class="fl two_t_m conpany" @click="$router.push('/personalCenter/invoicesuccess')">深圳市沃菲数字营销有限…</span>
          <span class="fr two_t_m" @click="$router.push('/personalCenter/invoicesuccess')">></span>
      </div> 
      <div class="time paddi">
          <div class="top">
              <p class="three_t_m">订单编号：<span class="three_t_h">0123456</span></p>
              <p class="three_t_m">下单日期：<span class="three_t_h">2017-12-20  10:00</span></p>
          </div>
          <div class="bottom">
              <p class="three_t_m">支付方式：<span class="three_t_h">微信支付</span></p>
              <p class="three_t_m">价格总计：<span class="three_t_h">¥57.00</span></p>
              <p class="three_t_m">配送费用：<span class="three_t_h">¥0.00</span></p>
              <p class="three_t_m">账户余额抵扣：<span class="three_t_h">-¥10.00</span></p>
              <p class="total three_t_tip">总计：<span class="price">¥47.00</span></p>
          </div>
      </div>
      <!-- 底部 -->
      <div class="operation clearfix">
          <button class="fr three_t_m grey">查看评价</button>
          <button class="fr three_t_m grey">配送详情</button>
          <button class="fr three_t_m grey">删除订单</button>
      </div>
  </div>
</template>

<script>
import { Flow, FlowState, FlowLine } from "vux";

export default {
  name: "Orderdetail",
  data() {
    return {};
  },
  methods: {},
  components: {
    Flow,
    FlowState,
    FlowLine
  }
};
</script>

<style scoped lang="less">
.orderdetail {
  font-size: 0.16rem;
  background-color: #f2f2f2;
  padding-bottom: 0.65rem;
  .paddi {
    padding: 0 0.27rem 0 0.28rem;
  }
  .flow {
    background-color: #fff;
  }
  .addressee {
    background-color: #fff;
    margin: 0.1rem 0rem;
    height: 0.86rem;
    padding-top: 0.15rem;
    padding-bottom: 0.1rem;
    .top {
      margin-bottom: 0.05rem;
      .name {
        margin-right: 0.3rem;
      }
    }
  }
  .goods {
    background: #ffffff;
    .state {
      padding: 0.15rem 0 0.1rem 0rem;
      border-bottom: 0.01rem solid #f2f2f2;
    }
    .bottom {
      padding: 0.1rem 0 0.15rem 0;
      img {
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 0.12rem;
      }
      .right {
        margin-left: 0.15rem;
        .guige {
          margin: 0.05rem 0 0.1rem 0;
        }
      }
    }
  }
  .fapiao {
    padding-top: 0.15rem;
    padding-bottom: 0.15rem;
    background-color: #fff;
    margin: 0.1rem 0;
    .conpany {
      width: 2rem;
      margin-left: 0.3rem;
    }
  }
  .time {
    background-color: #fff;
    .top {
      padding: 0.15rem 0 0.05rem 0rem;
      p {
        margin-bottom: 0.05rem;
      }
    }
    .bottom {
      padding: 0.1rem 0 0.05rem 0rem;
      border-top: 0.01rem solid #f2f2f2;
      p {
        margin-bottom: 0.05rem;
      }
      .total {
        border-top: 0.01rem solid #f2f2f2;
        padding: 0.1rem 0 0.15rem 0rem;
        margin: 0rem;
        .price {
          font-size: 0.16rem;
        }
      }
    }
  }
  .operation {
    height: 0.54rem;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    padding-right: 0.27rem;
    .grey {
      border: 0.01rem solid #7f7f7f;
      border-radius: 0.03rem;
      padding: 0.03rem 0.06rem 0.04rem 0.06rem;
      background-color: #ffffff;
      margin: 0.15rem 0 0.15rem 0.2rem;
    }
  }
}
</style>



